import React from "react";

import axios from 'axios';
import { ValidatorForm } from 'react-form-validator-core';
import {Spring} from "react-spring/renderprops";
const url = `https://urfu-events-project.herokuapp.com/test/`;


class EventForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name:'',
            group:'',
            nameError:'',
            groupError:''
        }
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleUserInputGroup = this.handleUserInputGroup.bind(this);
    }

    handleUserInputGroup = (event) => {
        this.setState({
            [event.target.name] : event.target.value
        });
    }

    validate = () => {
        // let nameError = '';
        let groupError = '';

        if (!this.state.group.includes('@')) {
            groupError = 'invalid group';
        }

        if (groupError) {
            this.setState({groupError});
            return false;
        }

        return true;
    }

    handleSubmit(event) {
        event.preventDefault();
        const isValid = this.validate();
        if (isValid) {
            console.log(this.state);
        }
    }

    // submit() {
    //     fetch(url,{
    //         method:'POST',
    //         headers: {
    //             'Content-Type': 'application/json'
    //         },
    //         body: JSON.stringify({group:15})
    //     })
    //         .then(res => {
    //             return res.json()
    //     })
    //         .then(data => console.log(data))
    //         .catch(error => console.log('error'))
    // }

    submit() {
        axios.post(url, {
            group:'15'
        }).then(res => { console.log(res) })
    }


    render() {
        return (
            <Spring
                from={{marginBottom:'500px'}}
                to = {{marginBottom:'-500px'}}
            >
                {props => (
                    <div style={props}>
                        <div className='event-form'>
                            <h2>Инструкция по записи на мероприятие</h2>
                            <ol>
                                <li>Введите существующую академическую группу</li>
                                <li>Введите свое имя и фамилию</li>
                                <li>Нажмите на кнопку <span>отправить</span></li>
                                <li>Обязательные для заполнения поля отмечены звездочкой</li>
                            </ol>
                            <form onSubmit={this.handleSubmit}>
                                <div className='container-input'>
                                        <input onChange={this.handleUserInputGroup}
                                               className='input-group'
                                               value={this.state.group}
                                               placeholder='Введите свою группу'
                                               name='group'
                                        />
                                        <div>{this.state.groupError}</div>
                                        <span className='asteriks1'>*</span>
                                        <input onChange={this.handleUserInputGroup}
                                               className='input-name'
                                               value={this.state.name}
                                               placeholder='Введите свое имя'
                                               name='name'
                                        />
                                        <span className='asteriks2'>*</span>
                                        <button type='submit' className='send-form'
                                               onClick={() => {this.props.handleClickSubmit()}}
                                        >{!this.props.isSubmitted ? 'Отправить' : 'Отправлено'}</button>
                                </div>
                            </form>
                        </div>
                    </div>
                )}
            </Spring>
        )
    }
}

export default EventForm;


