import React from "react";

import first from '../img/first.jpg'
import second from '../img/second.jpg'
import third from '../img/third.jpg'

import * as axios from 'axios';

import ShowCardDescription from "./card-show-onclick";
import Description from "./description";
import {Link, Route, BrowserRouter, Switch} from "react-router-dom";

const styles = {
    color:'#000'
}

const apiURL = `https://urfu-events-project.herokuapp.com/api/events/`;

class MainContent extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isToggleOn: true,
            card:[]
        };
        this.handleClick = this.handleClick.bind(this);
    }

    handleClick() {
        this.setState({
            isToggleOn: !this.state.isToggleOn
        });
    }

    componentDidMount() {
        axios.get(apiURL).then(response => {
            const card = response.data;
            this.setState({card});
        })
    }



    render() {
            return (
                <BrowserRouter>
                    <div id="tabs-content">
                        <div className="inside-container block active tab-1">
                            <div id='wrapp' >
                                {
                                    this.state.card.map(event_id => {
                                        return (
                                            <Link style={styles} to={"/api/events/" + event_id.pk}>
                                                <ShowCardDescription
                                                    idx={event_id}
                                                    isToggleOn={this.state.isToggleOn}
                                                    handleClick={this.handleClick}
                                                    name={event_id.title}
                                                    time={event_id.datetime}
                                                    place={event_id.place}
                                                    image={(event_id.pk === 5 && second)
                                                    || (event_id.pk === 8 && first)
                                                    || (event_id.pk === 2 && third)}
                                                >
                                                    <Switch>
                                                        <Route path='/' exact render={() => <MainContent /> } />
                                                        <Route
                                                            path="/api/events/:event_id"
                                                            render={props => {
                                                                return <Description
                                                                    name={event_id.title}
                                                                    date={event_id.datetime}
                                                                    place={event_id.place}
                                                                    member={event_id.members_count}
                                                                    description={event_id.description}
                                                                    image={(event_id.pk === 5 && second)
                                                                    || (event_id.pk === 8 && first)
                                                                    || (event_id.pk === 2 && third)}
                                                                />
                                                            }}
                                                        />
                                                    </Switch>
                                                </ShowCardDescription>
                                            </Link>
                                        );
                                    })
                                }
                            </div>
                        </div>

                        <div className="inside-container block tab-2 ">
                            <div id='wrapp'>
                                {
                                    this.state.card.map(event_id => {
                                        return (
                                            <Link style={styles} to={"/api/events/" + event_id.pk}>
                                                <ShowCardDescription
                                                    idx={event_id}
                                                    isToggleOn={this.state.isToggleOn}
                                                    handleClick={this.handleClick}
                                                    name={event_id.title}
                                                    time={event_id.datetime}
                                                    place={event_id.place}
                                                    image={(event_id.pk === 5 && second)
                                                    || (event_id.pk === 8 && first)
                                                    || (event_id.pk === 2 && third)}
                                                >
                                                    <Switch>
                                                        <Route path='/' exact render={() => <MainContent /> } />
                                                        <Route
                                                            path="/api/events/:event_id"
                                                            render={props => {
                                                                return <Description
                                                                    name={event_id.title}
                                                                    date={event_id.datetime}
                                                                    place={event_id.place}
                                                                    member={event_id.members_count}
                                                                    description={event_id.description}
                                                                    image={(event_id.pk === 5 && second)
                                                                    || (event_id.pk === 8 && first)
                                                                    || (event_id.pk === 2 && third)}
                                                                />
                                                            }}
                                                        />
                                                    </Switch>
                                                </ShowCardDescription>
                                            </Link>
                                        );
                                    })
                                }
                            </div>
                        </div>
                    </div>
                </BrowserRouter>
            )
    }
}

export default MainContent;