import React from "react"

import '../styles/auth-style.css'
import crossImage from "../img/x.svg"

const styles = {
    afterClick:{visibility:'hidden'},
    beforeClick:{visibility:'visible'},
    w: {
        color: '#ff4444',
        fontWeight: 'bold'
    },
    m: {
        color: '#4285F4',
        fontWeight: 'bold'
    },
    s: {
        color: '#00C851',
        fontWeight: 'bold'
    }

}


class Authorisation extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isClickedOutside: false,
            redirect:false,
            email:'',
            password:'',
            pwd: "неизвестно"
        }
        this.handleClickBackground = this.handleClickBackground.bind(this);
        this.handleUserInput = this.handleUserInput.bind(this);
        this.handleChange = this.handleChange.bind(this);
    }


    handleClickBackground = (e) => {
        if (e.target.className==='overlay' || e.target.className==='close authorisation') {
            this.setState( {isClickedOutside: !this.state.isClickedOutside})
        }
    }

    handleUserInput = (event) => {
        this.setState({
            [event.target.name] : event.target.value
        });
    }

    handleChange(e){
        let password = e.target.value;
        let capitals = /[A-Z]/,
            smalls = /[a-z]/,
            nums  = /[0-9]/;
        if(password.length < 8){
            return this.setState({ pwd: "низкая" });
        }else if(smalls.test(password) && nums.test(password) && capitals.test(password)){
            this.setState({
                pwd: "высокая"
            });
        }else if(smalls.test(password) && nums.test(password)){
            this.setState({
                pwd: "средняя"
            });
        }else if(capitals.test(password) && nums.test(password)){
            this.setState({
                pwd: "средняя"
            });
        }else if(capitals.test(password) && smalls.test(password)){
            this.setState({
                pwd: "средняя"
            });
        }else if(capitals.test(password)){
            this.setState({
                pwd: "низкая"
            });
        }else if(smalls.test(password)){
            this.setState({
                pwd: "низкая"
            });
        }else if(nums.test(password)){
            this.setState({
                pwd: "низкая"
            });
        }
    }

    render() {
        let strength = this.state.pwd;
        return (
            <div className={'overlay'} onClick={this.handleClickBackground} style={this.state.isClickedOutside ? styles.afterClick : styles.beforeClick} >
                <form>
                    <div className="login-form">
                        <img style={{padding:'7px'}} alt={'close'} className='close authorisation' src={crossImage} />
                        <p className="enter">Вход</p>
                        <a href='http://urfu-events-project.herokuapp.com/social/login/vk-oauth2' className="vk">Вконтакте</a>
                        <div className="info-login">
                            <input required
                                   className='input-email'
                                   type='email' name='email'
                                   placeholder="Почта"
                                   value={this.state.email}
                                   onChange={this.handleUserInput}
                            />
                            <input required
                                   className='input-pass'
                                   type="password"
                                   name='password'
                                   maxLength='15'
                                   minLength='6'
                                   placeholder="Пароль"
                                   onChange={this.handleChange}
                            />
                            <p style={{textAlign:'center'}}>Надежность: <span style={strength?strength==='низкая'?styles.w:strength==='средняя'?styles.m:strength==='высокая'?styles.s:styles.m:styles.m
                            }>{strength}</span></p>
                            <a className="iForgotPassword" href="/#">забыли пароль?</a>
                        </div>
                        <button className="enter-button">Войти</button>
                    </div>
                </form>
            </div>
        );
    }
}

export default Authorisation;